import axios from 'axios';
import router from '../router';
import iconv from "vue-template-compiler/browser";
axios.defaults.timeout = 5000;  //è¶…å¸‚æ—¶é—´æ˜¯5ç§’
axios.defaults.withCredentials = true;  //å…è®¸è·¨åŸŸ
//Content-Type å“åº”å¤´
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
//åŸºç¡€url
axios.defaults.baseURL = "http://43.153.63.130:8888";
// axios.defaults.baseURL = "http://localhost:8888";
//å“åº”æ‹¦æˆªå™¨
axios.interceptors.response.use(
    response => {
        //å¦‚æœreponseé‡Œé¢çš„statusæ˜¯200ï¼Œè¯´æ˜è®¿é—®åˆ°æ¥å£äº†ï¼Œå¦åˆ™é”™è¯¯
        if(response.status == 200){
            return Promise.resolve(response);
        }else{
            return Promise.reject(response);
        }
    },
    error => {
        if(error.response.status){
            switch(error.response.status){
                case 401:       //æœªç™»å½•
                    router.replace({
                        path:'/',
                        query:{
                            redirect: router.currentRoute.fullPath
                        }
                    });
                    break;
                case 404:   //æ²¡æ‰¾åˆ°
                    break;
            }
            return Promise.reject(error.response);
        }
    }
);

/**
 * å°è£…getæ–¹æ³•
 */
export function get(url,params={}){
    return new Promise((resolve,reject) => {
        axios.get(url,{params:params})
        .then(response =>{
          resolve(response.data);
        })
        .catch(err =>{
            reject(err);
        })
    });
}

/**
 * å°è£…postæ–¹æ³•
 */
export function post(url,data={}){
    return new Promise((resolve,reject) => {
        axios.post(url,data)
        .then(response =>{
            resolve(response.data);
        })
        .catch(err =>{
            reject(err);
        })
    });
}



